// index.js
import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App"; // Asegrate de que esta ruta est correcta dependiendo de tu estructura de carpetas
import "./styles/index.css"; // Si tienes un archivo de estilo global, asegrate de importarlo tambin
import { BrowserRouter as Router } from "react-router-dom"; // Si ests utilizando React Router para la navegacin

const root = ReactDOM.createRoot(document.getElementById("root")); // Verifica que el ID sea "root" en tu HTML

root.render(
  <React.StrictMode>
    <Router>
      <App />
    </Router>
  </React.StrictMode>
);
